///<amd-module name='ModuleDontChangeThisLine' />
import * as React from 'react';

import {
    ButtonType,
    ButtonWidget,
    LineTextFieldWidget,
} from '@tableau/widgets';

declare global {
    interface Window { tableau: any; }
}

let dashboard;

// Creates configuration page with dropdowns etc.
class Settings extends React.Component<any, any> {

    render() {
        return (
            <React.Fragment>
                <div className='container'>
                    <div>
                        <div className='header'>
                            Revert Filters | Configuration
                            <div className='tooltip'>
                                <svg xmlns='http://www.w3.org/2000/svg' id='Dialogs_x5F_Info' width='15' height='15' viewBox='0 0 15 15'>
                                    <rect id='Line' x='7' y='6' width='1' height='5' fillRule='evenodd' clipRule='evenodd' fill='#666766' />
                                    <rect id='Dot_2_' x='7' y='4' width='1' height='1' fillRule='evenodd' clipRule='evenodd' fill='#666766' />
                                    <path id='Circle' d='M7.5,1C3.9,1,1,3.9,1,7.5S3.9,14,7.5,14 S14,11.1,14,7.5S11.1,1,7.5,1z M7.5,13C4.5,13,2,10.5,2,7.5C2,4.5,4.5,2,7.5,2S13,4.5,13,7.5C13,10.5,10.5,13,7.5,13z' fillRule='evenodd' clipRule='evenodd' fill='#666766' />
                                </svg>
                                <span className='tooltiptext'>
                                    <b>How to Use</b>
                                    <ol>
                                        <li>Set your dashboard filters they way you want to be able to revert to.</li>
                                        <li>Open the configuration window and click "Save Settings".</li>
                                        <li>Optional: Customize your buttons label and colors.</li>
                                        </ol>
                                    <p>Note: You can add as many instances of this extension as you like!</p>
                                </span>
                            </div>
                        </div>
                        <div>
                            <div className='title'>Button Settings</div>
                            <div className='config'>
                                <LineTextFieldWidget text={this.props.label} handleChange={(text: string) => this.props.labelChange(text)} testId='label' floatingLabel='Label'/>
                                <p>Click <b>Save Settings</b> to save current filters.</p>
                                <div className='set'><ButtonWidget buttonType={ButtonType.Outline} handleClick={() => {this.props.getFilters(); this.props.showSaved();}} testId='set-filters'>Save Settings</ButtonWidget><span className={this.props.saved ? 'saved show' : 'saved'}>Settings saved!</span></div>
                                
                            </div>
                            <div className='title'>Formatting</div>
                            <div className='formatting'>
                                <p className='color'>Background Color <input type='color' defaultValue={this.props.bg} onChange={(color) => this.props.bgChange(color.target.value)} style={{backgroundColor: this.props.bg}}/></p>
                                <p className='color'>Button Color <input type='color' defaultValue={this.props.button} onChange={(color) => this.props.buttonChange(color.target.value)} style={{backgroundColor: this.props.button}}/></p>
                                <p className='color'>Button Text Color <input type='color' defaultValue={this.props.text} onChange={(color) => this.props.textChange(color.target.value)} style={{backgroundColor: this.props.text}}/></p>
                            </div>
                        </div>
                    </div>
                    <div className='footer'>
                        <div className='btncluster'>
                        <ButtonWidget buttonType={ButtonType.Go} handleClick={() => this.props.submit()} testId='ok' style={{marginLeft: '12px'}}>OK</ButtonWidget>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

// Container for all configurations
class Configure extends React.Component<any, any> {
    constructor(props) {
        super(props);
        this.state = {
            configured: false,
            filters: [],
            label: 'Revert Filters',
            bg: '#ffffff',
            button: '#2DCC97',
            text: '#ffffff',
            saved: false,
        };
        this.labelChange = this.labelChange.bind(this);
        this.bgChange = this.bgChange.bind(this);
        this.buttonChange = this.buttonChange.bind(this);
        this.textChange = this.textChange.bind(this);
    }

    // Handles change in label input
    labelChange(text) {
        this.setState({
            label: text,
        });
        window.tableau.extensions.settings.set('label', text);
        window.tableau.extensions.settings.saveAsync();
    }
    
    // Handles change in color input
    bgChange(color) {
        console.log(`Set background color to ${color}.`);
        this.setState({
            bg: color
        });
        window.tableau.extensions.settings.set('bg', color);
        window.tableau.extensions.settings.saveAsync();
    }

    // Handles change in color input
    buttonChange(color) {
        console.log(`Set button color to ${color}.`);
        this.setState({
            button: color
        });
        window.tableau.extensions.settings.set('button', color);
        window.tableau.extensions.settings.saveAsync();
    }

    // Handles change in color input
    textChange(color) {
        console.log(`Set button text color to ${color}.`);
        this.setState({
            text: color
        });
        window.tableau.extensions.settings.set('text', color);
        window.tableau.extensions.settings.saveAsync();
    }

    // Get all filters from all worksheets in dashboard
    getFilters() {
        let filterFetchPromises = [];
        let dashboardfilters = [];
        dashboard.worksheets.forEach(worksheet => {
            filterFetchPromises.push(worksheet.getFiltersAsync());
        });
        Promise.all(filterFetchPromises).then(fetchResults => {
            fetchResults.forEach(filtersForWorksheet => {
                filtersForWorksheet.forEach(filter => {
                    dashboardfilters.push(filter);
                });
            });
            console.log(dashboardfilters);
            this.constructSettings(dashboardfilters);
        });
    }

    // Transforms filters into settings
    constructSettings(filters) {
        let settings = [];
        for (let f of filters) {
            let skip;
            switch (f.filterType) {
                case 'range':
                    let min = f.minValue.value;
                    let max = f.maxValue.value;
                    let nullOption = (f.maxValue.formattedValue === 'Null' && f.minValue.formattedValue === 'Null') ? 'all-values' : '';
                    skip = (f.minValue.value === undefined || f.minValue.value instanceof Date || f.fieldName.startsWith('Action (')) ? true : false;
                    settings.push({
                        worksheetName: f.worksheetName,
                        fieldName: f.fieldName,
                        filterType: f.filterType,
                        min: (min === 0) ? .0000000000001 : min,
                        max: (max === 0) ? .0000000000001 : max,
                        nullOption: nullOption,
                        skip: skip,
                    });
                    break;
                case 'categorical':
                    let values = [];
                    for (let v of f.appliedValues) {
                        values.push(v.formattedValue)
                    }
                    let updateType = (values.length === 0) ? 'all' : 'replace';
                    skip = (f.fieldName === 'Measure Names' || f.fieldName.startsWith('Action (')) ? true : false; 
                    settings.push({
                        worksheetName: f.worksheetName,
                        fieldName: f.fieldName,
                        filterType: f.filterType,
                        isExcludeMode: f.isExcludeMode,
                        appliedValues: values,
                        updateType: updateType,
                        skip: skip,
                    });
                    break;
                default:
                    continue;
            }
        }
        window.tableau.extensions.settings.set('filters_set', 'true');
        window.tableau.extensions.settings.set('filters', JSON.stringify(settings));
        window.tableau.extensions.settings.saveAsync();
    }
    
    // Saves settings and closes configure dialog with data source payload
    submit() {
        let text = (this.state.label || 'Revert Filters');
        window.tableau.extensions.settings.set('label', text);
        window.tableau.extensions.settings.saveAsync();
        window.tableau.extensions.ui.closeDialog('closed');
    }

    showSaved() {
        this.setState({
            saved: true,
        });
        setTimeout(() => {
            this.setState({
                saved: false,
            });
        }, 1000);
    }

    // Once we have mounted, we call to initialize
    componentWillMount() {
        let uiPromise = window.tableau.extensions.initializeDialogAsync();
        if (uiPromise) {
            uiPromise.then((openPayload) => {
                dashboard = window.tableau.extensions.dashboardContent.dashboard;
                let settings = window.tableau.extensions.settings.getAll();
                this.setState({
                    configured: settings.configured === 'true',
                    filters: JSON.parse(settings.filters),
                    bg: settings.bg,
                    button: settings.button,
                    text: settings.text,
                    label: settings.label,
                });
                this.getFilters()
            });
        } else {
          console.log('Not running inside of window.tableau');
        }
    }

    render() {
      return (
            <Settings
                submit={() => this.submit()}
                getFilters={() => this.getFilters()}
                showSaved={() => this.showSaved()}
                labelChange={this.labelChange}
                bgChange={this.bgChange}
                buttonChange={this.buttonChange}
                textChange={this.textChange}
                configured={this.state.configured}
                bg={this.state.bg}
                button={this.state.button}
                text={this.state.text}
                label={this.state.label}
                saved={this.state.saved}
            />
      );
    }
}

export default Configure;