///<amd-module name="ModuleDontChangeThisLine" />
import * as React from 'react';

declare global {
    interface Window { tableau: any; }
}

let dashboard;

class RevertFilters extends React.Component<any, any> {
    constructor(props) {
        super(props);
        this.state = {
            configured: false,
            filters: [],
            label: 'Revert Filters',
            bg: '#ffffff',
            button: '#2DCC97',
            text: '#ffffff',
        };
    }

    // Clears all filters on a dashboard
    clearFilters() {
    dashboard.worksheets.forEach(worksheet => {
        worksheet.getFiltersAsync().then(filtersForWorksheet => {
            let filterClearPromises = [];
            filtersForWorksheet.forEach(filter => {
                filterClearPromises.push(worksheet.clearFilterAsync(filter.fieldName));
            });
        });
    });
}

    applyFilters() {
        let settings = JSON.parse(window.tableau.extensions.settings.get('filters'));
        for (let d of settings) {
            if (d.skip === false) {
                switch (d.filterType) {
                    case 'range':
                        let filterOptions = {};
                        if (d.nullOption === 'all-values') {
                            filterOptions = { max: 1, min: 1, nullOption: d.nullOption };
                        } else {
                            filterOptions = { max: d.max, min: d.min};
                        }
                        dashboard.worksheets.find(ws => ws.name === d.worksheetName).applyRangeFilterAsync(d.fieldName, filterOptions);
                        break;
                    case 'categorical':
                        dashboard.worksheets.find(ws => ws.name === d.worksheetName).applyFilterAsync(d.fieldName, d.appliedValues, d.updateType, { isExcludeMode: d.isExcludeMode });
                        break;
                    default:
                        continue;
                }
            }
        }
    }

    // Pops open the configure page
    configure() {
        const popupUrl = `${window.location.origin}/Revert-Filters/#/config`;
        let payload;
        window.tableau.extensions.ui.displayDialogAsync(popupUrl, payload, { height: 385, width: 285 }).then((closePayload) => {
            console.log('Configure closed.');
            console.log(window.tableau.extensions.settings.getAll());
        }).catch((error) => {
            switch (error.errorCode) {
                case window.tableau.ErrorCodes.DialogClosedByUser:
                    console.log('Dialog was closed by user.');
                    break;
                default:
                    console.error(error.message);
            }
        });
    }

    // Update buttons from configuration settings
    updateSettings(settings) {
        console.log('Update settings!', window.tableau.extensions.settings.getAll())
        this.setState({
            configured: settings.configured === 'true',
            filters: JSON.parse(settings.filters),
            bg: settings.bg,
            button: settings.button,
            text: settings.text,
            label: settings.label,
        });
    }

    // Once we have mounted, we call to initialize
    componentWillMount() {
        let initialziePromise = window.tableau.extensions.initializeAsync({ configure: this.configure });
        if (initialziePromise) {
            initialziePromise.then(() => {
                window.tableau.extensions.settings.addEventListener(window.tableau.TableauEventType.SettingsChanged, (settingsEvent) => {
                    this.updateSettings(settingsEvent.newSettings)
                });
                dashboard = window.tableau.extensions.dashboardContent.dashboard;
                let configured = (window.tableau.extensions.settings.get('configured') === 'true');
                if (!configured) {
                    window.tableau.extensions.settings.set('filters', JSON.stringify(this.state.filters));
                    window.tableau.extensions.settings.set('bg', this.state.bg);
                    window.tableau.extensions.settings.set('button', this.state.button);
                    window.tableau.extensions.settings.set('text', this.state.text);
                    window.tableau.extensions.settings.set('label', this.state.label);
                    window.tableau.extensions.settings.set('configured', 'true');
                    window.tableau.extensions.settings.saveAsync().then(result => {
                        this.configure();
                    });
                } else {
                    this.updateSettings(window.tableau.extensions.settings.getAll())
                }
          });
        }
    }

    hexToRgb(hex) {
        let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
        return result ? {
            r: parseInt(result[1], 16),
            g: parseInt(result[2], 16),
            b: parseInt(result[3], 16),
        } : null;
    }

    render() {
        let rgb = this.hexToRgb(this.state.button);
        let active = `rgb(${Math.floor(rgb.r)}, ${Math.floor(rgb.g)}, ${Math.floor(rgb.b)})`;
        let color = `rgba(${Math.floor(rgb.r)}, ${Math.floor(rgb.g)}, ${Math.floor(rgb.b)}, .8)`;
        let css = `
            button.button:active {
                background-color: `+ active +` !important;
            }
        `
      return (
        <div className='outer' style={{backgroundColor: this.state.bg}}>
            <div className='inner'>
            <style>{css}</style>
                <button className='button' onClick={() => this.applyFilters()} style={{backgroundColor: color, color: this.state.text}}>{this.state.label}</button>
            </div>
        </div>
      );
    }
}

export default RevertFilters;